// @flow
import React, { Component } from "react";

import Href from "beinformed/models/href/Href";

type RouterProps = {
  fullPageForms: boolean,
  hasCaseManagementTab: boolean,
  hasForm: boolean,
  url: Href,
  onPopState: (href: Href) => void,
  renderer: any
};

/**
 * Router configuration
 */
class Router extends Component<RouterProps> {
  /**
   * Handle popstate events to resync the reducers
   */
  componentDidMount() {
    window.addEventListener("popstate", this.handlePopState);
  }

  /**
   * Remove listener for popstate event
   */
  componentWillUnmount() {
    window.removeEventListener("popstate", this.handlePopState);
  }

  /**
   * Handle popstate event
   */
  handlePopState = () => {
    this.props.onPopState(new Href(location.href));
  };

  componentToRender() {
    if (this.props.fullPageForms && this.props.hasForm) {
      return "Form";
    }

    const isRoot = this.props.url.path.split("/").length - 1 === 1;
    const isModelCatalog =
      this.props.url.startsWith(new Href("/modelcatalog")) ||
      this.props.url.startsWith(new Href("/concepts"));
    const isContentBrowser =
      this.props.url.startsWith(new Href("/contentbrowser")) ||
      this.props.url.startsWith(new Href("/content"));

    if (isModelCatalog) {
      return "ModelCatalog";
    } else if (isContentBrowser) {
      return "ContentBrowser";
    } else if (this.props.hasCaseManagementTab) {
      return "Tab";
    } else if (isRoot) {
      return "Root";
    }

    return "NotFound";
  }

  renderForm() {
    if (this.props.hasForm && !this.props.fullPageForms) {
      const Renderer = this.props.renderer;
      return (
        <Renderer
          componentType="Form"
          url={this.props.url}
          fullPageForms={this.props.fullPageForms}
        />
      );
    }

    return null;
  }

  render() {
    if (!this.props.url) {
      throw new Error("Url must be set to use the Router");
    }

    // sync browser url with the url in the reducer of the router
    if (
      this.props.url !== null &&
      typeof location !== "undefined" &&
      this.props.url.absolutehref !== location.href
    ) {
      history.pushState(null, "", this.props.url.absolutehref);
    }

    const Renderer = this.props.renderer;
    return (
      <div>
        <Renderer
          componentType={this.componentToRender()}
          url={this.props.url}
          fullPageForms={this.props.fullPageForms}
        />
        {this.renderForm()}
      </div>
    );
  }
}

export default Router;



